<template>
    <div>
        <div class="header">
          <van-nav-bar
  title="注册"
  left-text="返回"
  right-text="登录"
  @click-right="go('/login')"
  @click-left="$router.go(-1)"
  left-arrow
/>  
        </div>

    <div class="text">
        <h1>你好，</h1>
        <h1>欢迎来到茶语</h1>
    </div>
        
        <div class="register">
<van-form >
      <van-field
        v-model="userInfo.phone"
        type="text"
        label="手机号"
        placeholder="输入11位手机号"
        left-icon="phone-o"
        maxlength="11"
        :rules="[]"
      />
      <van-field
        v-model="userInfo.nickName"
        type="text"
        label="昵称"
        placeholder="汉字字母数字_组合(1-10位)"
        left-icon="contact"
        maxlength="10"
         :rules="[]"
      />
      <van-field
        v-model="userInfo.password"
        :type="isbool ? 'text' : 'password'"
        label="密码"
        placeholder="密码至少包含数字和英文(6-20位)"
        left-icon="lock"
        :right-icon="isbool ? 'eye-o' : 'closed-eye'"
        @click-right-icon="viewPassword"
        maxlength="16"
         :rules="[]"
      />
      <div class="register-btn">
        <van-button round block type="info" native-type="submit" @click="register">立即注册</van-button>
      </div>
    </van-form>
<div class="tips">
    <p>注册表示接受<font>《版权声明》</font>和<font>《隐私保护》</font></p>
</div>
        </div>

        <div class="ways">
            <van-divider>第三方登录</van-divider>
            <van-row>
  <van-col span="8"><span class="iconfont icon-qq"></span></van-col>
  <van-col span="8"><span class="iconfont icon-weixin"></span></van-col>
  <van-col span="8"><span class="iconfont icon-tubiaozhizuo-"></span></van-col>
</van-row>
        </div>
    </div>
</template>

<script>
  import Testing from '../assets/js/testing'
    export default {
          data() {
    return {
       userInfo: {
          phone: '',
          nickName: '',
          password: ''
        },
      isbool:true,
    };
  },
  methods: {
    register() {
     
       if (Testing.Testing(this.userInfo, this)) {
         
         //简写
         let data=Object.assign({},this.userInfo)
         data.appkey=this.appkey
         

        this.$toast.loading({
      message: '加载中...',
      forbidClick: true,
      duration:0
    });
         this.axios({
           method:"post",
           url:'/register',
           //传统写法
          //  data:{
          //     phone: this.userInfo.phone,
          // nickName: this.userInfo.nickName,
          // password: this.userInfo.password,
          // appkey:'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA='
          //  }
          data
         }).then((res)=>{
           
           if (res.data.code == 100) {
               this.$toast.clear();
              this.$router.push({name: '登录'});
            } else {
              this.$toast(res.data.msg);
            }
         }).catch((err)=>{
            
         })
       }
    },
    viewPassword(){
        this.isbool=!this.isbool
    },
    go(name){
      this.$router.push(name)
    }
  },
    }
</script>

<style lang="less" scoped>
    .text{
        width: 80%;
        margin:0 auto;
        margin-top: 80px;
        margin-bottom:50px;
        color: #263434;
        h1{
            margin: 0;
            padding: 0;
            font-weight: bold;
            font-size: 28px;
        }
    }
    .register{
        width: 85%;
        margin: 0 auto;
        color: #505c5c;
    }
   .van-cell{
        font-weight: bolder;
    }
    .tips{
         p{
             text-align: center;
             color:#707070;
      font{
        color:#c81522
    }  
    }
    }
    .ways{
        width: 80%;
        margin: 0 auto;
        .van-col{
            text-align: center;
            .iconfont{
                display: block;
                color: #707070;
                font-size: 20px;
            }
        }
    }
   input{
    color:black
   }
    
</style>